import { PrismaAdapter } from '@auth/prisma-adapter';

import { type Adapter } from 'next-auth/adapters';
import Cognito from 'next-auth/providers/cognito';
import { db } from '@com/db';
import { DefaultSession, Session, User } from 'next-auth';

declare module 'next-auth' {
  interface Session extends DefaultSession {
    user: {
      id: string;
      family_name: string;
      given_name: string;
      image: string;
      roles: string[];
      email: string;
      job_title: string;
      // Additional properties can be defined here
    } & DefaultSession['user'];
  }

  interface User {
    family_name: string;
    given_name: string;
    image: string;
    roles: string[];
    email: string;
    job_title: string;
    // Additional properties can be defined here
  }
}
// Your database client must be provided for the PrismaAdapter

export const authConfig = {
  callbacks: {
    session: ({ session, user }: { session: Session, user: User }) => ({
      ...session,
      user: {
        ...session.user,
        id: user.id,
        family_name: user.family_name,
        given_name: user.given_name,
        image: user.image,
        roles: user.roles,
        email: user.email,
        job_title: user.job_title,
      },
    }),
  },
  adapter: PrismaAdapter(db) as Adapter,
  providers: [
    Cognito({
      clientId: process.env.COGNITO_CLIENT_ID!,
      clientSecret: process.env.COGNITO_CLIENT_SECRET!,
      checks: ['none'],
      profile(profile) {
        console.log('----------profile----------');
        console.log(profile);
        return {
          id: profile.sub,
          name: `${profile.given_name} ${profile.family_name}`,
          given_name: profile.given_name,
          family_name: profile.family_name,
          job_title: profile['custom:job_title'],
          username: profile['cognito:username'],
          roles: profile['cognito:groups'],
          image: profile.picture,
          email: profile.email,
        };
      },
      issuer:
        `https://cognito-idp.${process.env.COGNITO_REGION}.amazonaws.com/` +
        process.env.COGNITO_USER_POOL_ID,
    }),
    /**
     *
     * ...add more providers here.
     *
     * Most other providers require a bit more work than the Discord provider. For example, the
     * GitHub provider requires you to add the `refresh_token_expires_in` field to the Account
     * model. Refer to the NextAuth.js docs for the provider you want to use. Example:
     *
     * @see https://next-auth.js.org/providers/github
     */
  ],
};
